
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
components: {
XHeader,
XTable,
LoadMore

},
data() {
return {
count: 0,
content: "分数为0分，低危，不予抗栓治疗。（参见下表）"
}
},
methods: {
radios: function() {
let _this = this;
_this.count = 0;
$("input:checked").each(function() {
_this.count = _this.count + Number($(this).val());
})
if(_this.count == "0") {
_this.content = "分数为" + _this.count + "分，低危，不予抗栓治疗。（参见下表）";
} else if(_this.count == "1") {
_this.content = "分数为" + _this.count + "分，中危，基于出血风险评估及患者意愿，考虑OAC。（参见下表）";
} else if(_this.count >= "2") {
_this.content = "分数为" + _this.count + "分，高危，若无禁忌症，推荐OAC。（参见下表）";
}
}
}
}



