
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
components: {
XHeader,
XTable,
LoadMore
},
data() {
return {
count: 0,
conent:"分数为2分，低危，抗血小板治疗推荐阿司匹林。（参见下表）"
}
},
methods: {
radios: function() {
var _this = this;
_this.count = 0;
$("input:checked").each(function() {
_this.count = _this.count + Number($(this).val());
})
if(_this.count < "3"){
_this.conent = "分数为"+_this.count+"分，低危，抗血小板治疗推荐阿司匹林。（参见下表）";
}else if(_this.count >="3" && _this.count<="6"){
_this.conent = "分数为"+_this.count+"分，中危，抗血小板治疗推荐氯吡格雷。（参见下表）";
}else{
_this.conent = "分数为"+_this.count+"分，高危，抗血小板治疗推荐阿司匹林+氯吡格雷。（参见下表）";
}
}
}
}



