
import XHeader from 'vux/src/components/x-header/index.vue'
import XTable from 'vux/src/components/x-table/index.vue'
import LoadMore from 'vux/src/components/load-more/index.vue'


export default {
components: {
XHeader,
XTable,
LoadMore
},
data() {
return {
count: 0,
conent:"分数为0-2分，低危险，静脉溶栓后症状性脑出血（SICH）率为0.4%。（参见下表）"
}
},
methods: {
radios: function() {
var _this =  this;
_this.count = 0;
$("input:checked").each(function() {

_this.count = _this.count+Number($(this).val());


})
if(_this.count >= 0 && _this.count <= 2){
_this.conent="分数为"+_this.count+"分，低危险，静脉溶栓后症状性脑出血（SICH）率为0.4%。（参见下表）";
}else if(_this.count >= 3 && _this.count <= 5){
_this.conent="分数为"+_this.count+"分，平均水平，静脉溶栓后症状性脑出血（SICH）率为1.5%。（参见下表）";
}else if(_this.count >= 6 && _this.count <= 8){
_this.conent="分数为"+_this.count+"分，平均水平，静脉溶栓后症状性脑出血（SICH）率为3.6%。（参见下表）";
}else if(_this.count >= 9){
_this.conent="分数为"+_this.count+"分，平均水平，静脉溶栓后症状性脑出血（SICH）率为9.2%。（参见下表）";
}
}
}
}



